.footer{
    
    @media (min-width:320px) {
        padding: 32px 0;
    }
    @media (min-width:768px){
        padding: 50px 0;
    }
    // height: 270px;
    background: #1E1823;
    &__title{
        margin: 0;
        width: 300px;
        color: #FFF;
        font-family: Montserrat;

        font-style: normal;
        font-weight: 500;

        &_span{
            color: #FD9222;
        }
        @media (min-width:320px) {
            font-size: 24px;
            line-height: 28px; 
            letter-spacing: -0.48px;
        }
        @media (min-width:768px) {
            font-size: 28px;
            line-height: 34px;
            letter-spacing: -0.56px;
            
        }
    }
    // &__title_span{
        
    //     font-family: Montserrat;
    //     font-size: 28px;
    //     font-style: normal;
    //     font-weight: 500;
    //     line-height: 34px; 
    //     letter-spacing: -0.56px;
    // }
    &__blocks{
        // padding-bottom: 58px;
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        @media (min-width:320px){
            gap: 32px;
            flex-wrap: wrap;
            margin-bottom: 32px;
        }
        @media (min-width:768px) {
            flex-wrap: nowrap;
            gap: 0;
            margin-bottom: 54px;
            
        }
        
    }
    &__block {
        
        
        // justify-content: center;
        @media (min-width:320px){
            width: 100%;
            display: flex;
            justify-content: space-between;
        }
        @media (min-width:768px) {
            width: unset;
            justify-content: unset;

            gap: 50px;
        }
        @media (min-width:1200px) {
            gap: 60px;
        }
    }

    &__icon-link path{
        stroke: #FFF;

    }
    &__hr{
        // width: 1136px;
        width: 100%;
        height: 0px;
        border-color: rgba(255, 255, 255, 0.10);
        margin: 0;
    }
    &__menu{
        margin: 0;
        display: inline-flex;
        align-items: flex-start;
        
        @media (min-width:320px){
            margin-top: 32px;
            flex-wrap: wrap;
            gap: 18px;
            width: 124px;
        }
        @media (min-width:768px){
            margin-top: 24px;
            width: unset;
            flex-wrap: nowrap;
            gap: 24px;

        }
    }
    &__item{
        text-decoration: none;
        color: #FFF;
        font-family: Montserrat;
        font-size: 14px;
        font-style: normal;
        font-weight: 500;
        line-height: 16px; /* 114.286% */
        letter-spacing: -0.28px;
        &:hover{
            color:#FD9222;
        }
    }
    &__contacts{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }
    &__phone{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 4px;
    }
    &__contact_title{
        text-decoration: none;
        color: #FFF;
        text-align: center;
        font-family: Montserrat;
        font-size: 14px;
        font-style: normal;
        font-weight: 500;
        line-height: 18px;
        margin: 0;
        margin-bottom: 14px;
    }
    &__contact_item{
        text-decoration: none;
        color: #FFF;
        text-align: center;
        font-family: Montserrat;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 18px;
        letter-spacing: -0.28px;
        &:hover{
            color:#FD9222;

        } 
    }

    &__icons{
        margin-bottom: 46px;
        display: flex;
        gap: 18px;
    }
}












