.backdrop {
  backdrop-filter: blur(5px);
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 100;
  transition: 0.5s;
}
.modal {
  transform: translate(-50%, -50%);
  position: absolute;
  top: 50%;
  left: 50%;
  border-radius: 15px;
  background: #fafafa;
  @media (min-width: 320px) {
    width: 311px;
    height: 471px;
  }
  @media (min-width: 720px) {
    width: 517px;
    height: 631px;
  }

  &__sudtitle {
    color: #111;
    font-family: Montserrat;
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: 24px;
    letter-spacing: -0.6px;
    text-transform: uppercase;

    @media (min-width: 768px) {
      font-size: 24px;

      line-height: 28px;
      letter-spacing: -0.72px;
    }
  }
