  &__btn {
    @include button;
    border: 0;
    width: 117px;
    height: 45px;
    margin-top: 18px;
    padding: 0;
    @media (max-width: 480px) {
      width: 93px;
      height: 41px;
    }
  }
  &__subpop {
    display: block;
    padding-right: 10px;
    position: fixed;
    top: 50%;
    width: 516px;
    height: 414px;
    left: 50%;
    transform: translate(-50%, -50%);
    img {
      position: absolute;
      top: 96px;
      left: 0;
    }
  }

  &__cross {
    position: absolute;
    border-radius: 100%;
    border: 0;
    top: 0;
    right: 0;
    padding: 28px;
  }
  &__titel {
    @include titel(24px, #fff);
  }
  &__desc {
    @include desc(18px, #fff);
  }

  &__span {
    color: #fd9222;




  }
