.is-hidden-thanks {
  opacity: 0;
  pointer-events: none;
}
textarea {
  overflow: hidden;
  border-radius: 13px;
  border: 1.5px solid rgba(17, 17, 17, 0.05);
  box-shadow: 0px 5.445px 5.445px 0px rgba(0, 0, 0, 0.03);
  resize: none;
  outline: none;
  color: rgba(64, 64, 64, 0.3);
  font-family: Montserrat;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: -0.36px;
  padding-top: 22px;
  width: 100%;
  // padding-left: 24px;
  @media (min-width: 320px) {
    height: 91px;
    // width: 235px;
    font-size: 14px;
  }
  @media (min-width: 768px) {
    height: 137px;
    // width: 410px;
    font-size: 18px;
  }
}
.modal-loved__input {
